import React, { useEffect, useState } from "react";
import { Input } from "../ui/input/input";
import { Button } from "../ui/button/button";
import { SolutionLayout } from "../ui/solution-layout/solution-layout";
import styles from "./string.module.css";
import { Circle } from "../ui/circle/circle";
import { ElementStates } from "../../types/element-states";
import { DELAY_IN_MS } from "../../constants/delays";
import { swap } from "./utils";

export const StringComponent: React.FC = () => {
  const [inputString, setInputString] = useState("");
  const [flag, setFlag] = useState(false); //   
  const [massivLetter, setMassivLetter] = useState<{ letter: string; color: ElementStates }[]>();

  //     
  useEffect(() => {
    return () => {
      onExpand();
    };
  }, []);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setInputString(event.target.value);
  };

  //     ''
  const onExpand = async () => {
    setFlag(true);
    const massiv = inputString.split(""); //   
    let arr = massiv.map((item) => {
      return { letter: item, color: ElementStates.Default };
    });
    let start = 0;
    let end = arr.length - 1;

    setMassivLetter([...arr]);
    await new Promise((resolve) => setTimeout(resolve, DELAY_IN_MS)); //      

    for (let i = start; i <= end; i++) {
      swap(arr, start, end, ElementStates.Changing, setMassivLetter);

      await new Promise((resolve) => setTimeout(resolve, DELAY_IN_MS)); //     circle
      start++;
      end--;
      setMassivLetter([...arr]);
    }
    setFlag(false);
  };

  return (
    <SolutionLayout title="">
      <div className={styles.content}>
        <Input
          placeholder=" "
          type="text"
          extraClass={styles.frame}
          maxLength={11}
          isLimitText
          onChange={handleChange}
          value={inputString}
        />
        <Button
          text=""
          linkedList="small"
          onClick={onExpand}
          disabled={inputString !== `` ? false : true}
          isLoader={!flag ? false : true}
        />
      </div>
      <div className={styles.circle}>
        {massivLetter &&
          massivLetter.map((item, index) => {
            return <Circle letter={item.letter} key={index} state={item.color} />;
          })}
      </div>
    </SolutionLayout>
  );
};
