import { ElementStates } from "../../types/element-states";

//  
export const randomArr = () => {
  const maxLen = 4; // max  
  const maxNum = 30; // max   
  const length = Math.round(Math.random() + maxLen);
  const max = Math.round(Math.random() * maxNum);
  return Array.apply(null, Array(length)).map(() => {
    return { element: Math.round(Math.random() * max), color: ElementStates.Default }; //   
  });
};
