import React, { useEffect, useState } from "react";
import { SHORT_DELAY_IN_MS } from "../../constants/delays";
import { ElementStates } from "../../types/element-states";
import { TPropItemInList } from "../../types/types";
import { Button } from "../ui/button/button";
import { Circle } from "../ui/circle/circle";
import { ArrowIcon } from "../ui/icons/arrow-icon";
import { Input } from "../ui/input/input";
import { SolutionLayout } from "../ui/solution-layout/solution-layout";
import { LinkedList } from "./list";
import styles from "./list-page.module.css";
import { randomArr } from "./utils";

export const ListPage: React.FC = () => {
  const [inputValue, setInputValue] = useState("");
  const [inputIndex, setInputIndex] = useState<number | undefined>();
  const [flag, setFlag] = useState(false); //    1
  const [flagTwo, setFlagTwo] = useState(false); //    2
  const [flagThree, setFlagThree] = useState(false); //    3
  const [flagFour, setFlagFour] = useState(false); //    4

  const list = new LinkedList<string>();

  const initArray = randomArr();
  const [isValues, setValues] = useState<TPropItemInList[]>(initArray);

  const handleChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.placeholder === " ") {
      setInputValue(event.target.value);
    } else {
      setInputIndex(Number(event.target.value));
    }
  };
  //     ''
  const addElement = async (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    setInputValue("");
    const arr = [...isValues];
    if (event.currentTarget?.name === "head") {
      setFlag(true);
      list.insertAt(inputValue, 0); //     
      const head = list.getIndex(0);
      list.print();
      //0   
      arr[0] = {
        ...arr[0],
        isHeadAdd: true,
        topCircle: {
          element: head ? head : inputValue,
        },
      };
      setValues([...arr]);
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      //       (topCircle)
      arr[0] = {
        ...arr[0],
        isHeadAdd: false,
        topCircle: undefined,
      };
      //    
      arr.unshift({
        element: head ? head : inputValue,
        color: ElementStates.Modified,
      });
      setValues([...arr]);
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      arr[0].color = ElementStates.Default; //    0 
      setFlag(false);
    }
    //      tail
    else if (event.currentTarget?.name === "tail") {
      setFlagTwo(true);
      let index = arr.length - 1;
      list.insertAt(inputValue, index); //     
      const tail = list.getIndex(index);
      list.print();
      //   
      arr[index] = {
        ...arr[index],
        isTailAdd: true,
        topCircle: {
          element: tail ? tail : inputValue,
        },
      };
      setValues([...arr]);
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      //       (topCircle)
      arr[index] = {
        ...arr[index],
        isTailAdd: false,
        topCircle: undefined,
      };
      //    
      arr.push({
        element: tail ? tail : inputValue,
        color: ElementStates.Modified,
      });
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      setValues([...arr]);
      arr[arr.length - 1].color = ElementStates.Default; //    0 
      setFlagTwo(false);
    }
  };

  //     ''
  const deleteElement = async (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    setInputValue("");
    const arr = [...isValues];
    //   
    if (event.currentTarget?.name === "headRemoved") {
      setFlagThree(true);
      list.print();
      //0   
      arr[0] = {
        ...arr[0],
        element: "",
        isHeadRemove: true,
        bottomCircle: {
          element: `${arr[0].element}`,
        },
      };
      setValues([...arr]);
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      //       (bottomCircle)
      arr[0] = {
        ...arr[0],
        isHeadRemove: false,
        bottomCircle: undefined,
      };
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      //    
      arr.shift();
      setValues([...arr]);
      setFlagThree(false);
      arr[0].color = ElementStates.Default; //    0 
    }
    //   
    else if (event.currentTarget?.name === "tailRemoved") {
      setFlagFour(true);
      let index = arr.length - 1;
      list.print();
      //   
      arr[index] = {
        ...arr[index],
        element: "",
        isTailRemove: true,
        bottomCircle: {
          element: `${arr[index].element}`,
        },
      };
      setValues([...arr]);
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      //       (bottomCircle)
      arr[index] = {
        ...arr[index],
        isTailRemove: false,
        bottomCircle: undefined,
      };
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      //    
      arr.pop();
      setValues([...arr]);
      setFlagFour(false);
      arr[arr.length - 1].color = ElementStates.Default; //    0 
    }
    setValues([...arr]);
  };

  //     '  '
  const setElementIndex = async (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    setInputIndex(Number(""));
    setInputValue("");
    const arr = [...isValues];
    let start = 0;
    list.print();
    //    
    if (event.currentTarget?.name === "addElementIndex" && inputIndex) {
      setFlagFour(true);
      while (start <= inputIndex) {
        //     
        arr[start] = {
          ...arr[start],
          isElementAdd: true,
          topCircle: {
            element: inputValue ? inputValue : "",
          },
        };
        setValues([...arr]);
        await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
        //       (topCircle)
        arr[start] = {
          ...arr[start],
          isElementAdd: false,
          topCircle: undefined,
        };
        start++;
      }
      //   
      arr.splice(inputIndex, 0, {
        element: inputValue ? inputValue : "",
        color: ElementStates.Modified,
      });
      setValues([...arr]);
      await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
      arr[inputIndex].color = ElementStates.Default; //    0 
      setFlagFour(false);
    }
    //    
    if (event.currentTarget?.name === "removeElementIndex" && inputIndex) {
      setFlagFour(true);
      list.print();
      while (start <= inputIndex) {
        //     
        arr[start] = {
          ...arr[start],
          isElementRemove: true,
          bottomCircle: {
            element: `${arr[inputIndex].element}`,
          },
          color: ElementStates.Changing,
        };
        setValues([...arr]);
        await new Promise((resolve) => setTimeout(resolve, SHORT_DELAY_IN_MS));
        //       (topCircle)
        arr[start] = {
          ...arr[start],
          isElementRemove: false,
          bottomCircle: undefined,
        };
        start++;
      }
      //   
      arr.splice(inputIndex, 1);
      arr.forEach((item) => (item.color = ElementStates.Default)); //     
      setValues([...arr]);
      setFlagFour(false);
    }
    setValues([...arr]);
  };

  return (
    <SolutionLayout title=" ">
      <div className={styles.content}>
        <Input
          placeholder=" "
          type="text"
          extraClass={styles.frame}
          maxLength={4}
          isLimitText
          onChange={handleChange}
          value={inputValue || ""}
        />
        <Button
          text="  head"
          linkedList="small"
          extraClass={styles.button}
          onClick={(event) => addElement(event)}
          name={`head`}
          isLoader={!flag ? false : true}
          disabled={inputValue ? false : true}
        />
        <Button
          text="  tail"
          linkedList="small"
          extraClass={styles.button}
          onClick={(event) => addElement(event)}
          name={`tail`}
          isLoader={!flagTwo ? false : true}
          disabled={inputValue ? false : true}
        />
        <Button
          text="  head"
          linkedList="small"
          extraClass={styles.button}
          name={"headRemoved"}
          onClick={(event) => deleteElement(event)}
          isLoader={!flagThree ? false : true}
          disabled={isValues.length !== 0 ? false : true}
        />
        <Button
          text="  tail"
          linkedList="small"
          extraClass={styles.button}
          name={"tailRemoved"}
          onClick={(event) => deleteElement(event)}
          isLoader={!flagFour ? false : true}
          disabled={isValues.length !== 0 ? false : true}
        />
        <Input
          placeholder=" "
          type="text"
          extraClass={styles.frame}
          maxLength={1}
          onChange={handleChange}
          value={inputIndex || ""}
          isLimitText
        />
        <Button
          text="  "
          linkedList="big"
          extraClass={styles.button__first}
          name={"addElementIndex"}
          onClick={(event) => setElementIndex(event)}
          disabled={inputIndex && inputValue ? false : true}
        />
        <Button
          text="  "
          linkedList="big"
          extraClass={styles.button__second}
          name={"removeElementIndex"}
          onClick={(event) => setElementIndex(event)}
          disabled={inputIndex && inputIndex < isValues.length ? false : true}
        />
      </div>
      <div className={styles.circle}>
        {isValues.map((item, index) => {
          return (
            <div className={styles.main} key={index}>
              <Circle
                letter={`${item.element}`}
                state={item.color}
                index={index}
                head={index === 0 && !item.isHeadAdd && !item.isHeadRemove ? "head" : ""}
                tail={index === isValues.length - 1 && !item.isTailAdd && !item.isTailRemove ? "tail" : ""}
              />
              {index !== isValues.length - 1 ? (
                <ArrowIcon fill={item.color === ElementStates.Changing ? "#D252E1" : "#0032FF"} />
              ) : null}
              {/*        */}
              {(item.isHeadAdd || item.isTailAdd || item.isElementAdd) && (
                <Circle
                  isSmall
                  letter={`${item.topCircle?.element}`}
                  state={ElementStates.Changing}
                  extraClass={styles.top}
                />
              )}
              {/*        */}
              {(item.isTailRemove || item.isHeadRemove || item.isElementRemove) && (
                <Circle
                  isSmall
                  letter={`${item.bottomCircle?.element}`}
                  state={ElementStates.Changing}
                  extraClass={styles.bottom}
                />
              )}
            </div>
          );
        })}
      </div>
    </SolutionLayout>
  );
};
